.imgDivStyle {
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

.fadeInStyle {
    width: 0;
    animation: fadeIn 5s forwards;
}

@keyframes fadeIn {
    from {
        width: 0;
    }
    to {
        width: 100%;
    }
}

.fadeInStyle-2 {
    opacity: 0;
    animation: fadeIn-2 3s forwards;
}

@keyframes fadeIn-2 {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

.fadeInStyle-3 {
    /* opacity: 0; */
    animation: fadeIn-3 3s forwards;
}

@keyframes fadeIn-3 {
    from {
        transform: scale(0);
    }
    to {
        transform: (1);
    }
}

.liStyle {
    list-style-type: none;
    counter-increment: item;
    margin-bottom: 5px
}

.liStyle::before {
    display:inline-block;
    width:1.25em;
    /* padding-right:0.5em; */
    font-weight:bold;
    /* text-align:right; */
    content:counter(item) ".";
}