.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  /* background-color: #282c34; */
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#menu-router {
  margin-left: 5vw;
  margin-right: 5vw;
  text-align: justify;
}

p::first-letter {
  margin-left: 1vw;
}

#title {
  color: #3da290;
  font-size: 24px;
  font-weight: 700;
  margin-left: 0;
}

.comp-name {
  color: #3da290;
  font-weight: bold;
}

footer {
  margin-left: 4vw;
  margin-right: 4vw;
  margin-top: 30px;
  font-size: 12px;
  color: #888;
}

footer > hr {
  box-shadow: 2px 2px 4px black;
  margin-bottom: 10px;
}

#comp {
  font-weight: bold;
}