.divFooter {
    display: flex;
    width: 100%;
    background: #ccc;
} 

.col {
    flex: 1;
    display: flex;
    padding-left: 2vw;
    padding-right: 2vw;
    font-size: 12px;
    color: #777;
    text-align: left;
    padding-bottom: 20px;
}

.col p {
    font-weight: bold;
    text-align: left;
}

.col p::first-letter {
    margin-left: 0;
}

.subcol {
    flex: 1;
}