.mainSubjects {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 0.5vw;
    row-gap: 1vh;
    height: 60vh;
    width: 30vw;
}

.divHomeContainer {
    display: flex;
    flex-direction: row;
}

.divParag {
    flex: 1;
    justify-content: center;
    align-items: center;
}

.divSubject {
    width: 100%;
    height: 100%;
    /* border: 1px solid gray; */
    /* border-radius: 2vw; */
}

.topLetfSubj {
    border-top-left-radius: 100%;
    /* box-shadow: inset 3px 3px 5px gray, inset -3px -3px 5px white; */
    background-image: url('../images/facility-11.jpg');
    background-size: cover;
}

.topRightSubj {
    border-top-right-radius: 100%;
    /* box-shadow: inset 3px -3px 5px gray, inset -3px 3px 5px white; */
    background-image: url('../images/facility-8.jpeg');
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
}

.bottomLeftSubj {
    border-bottom-left-radius: 100%;
    /* box-shadow: inset 3px 3px 5px gray, inset -3px 3px 5px white; */
    background-image: url('../images/facility-10.png');
    background-size: cover;
    /* background-position: center; */
    background-repeat: no-repeat;
}

.bottomRightSubj {
    border-bottom-right-radius: 100%;
    /* box-shadow: inset 3px 3px 5px gray, inset -3px -3px 5px white; */
    background-image: url('../images/facility-1.jpg');
    background-size: cover;
    background-position: center;
}

.fadeIn1 {
    opacity: 0;
    animation: fadeInQrt 1s 0.5s forwards;
}

.fadeIn2 {
    opacity: 0;
    animation: fadeInQrt 1s 1s forwards;
}

.fadeIn3 {
    opacity: 0;
    animation: fadeInQrt 1s 1.5s forwards;
}

.fadeIn4 {
    opacity: 0;
    animation: fadeInQrt 1s 2s forwards;
}

@keyframes fadeInQrt {
    from { opacity: 0; }
    to { opacity: 1; }
}

