.menuContainer {
    position: sticky;
    top: 0;
    left: 0;
    display: flex; 
    flex-direction: row;
    align-items: center;
    justify-content: center;
    background: white;
    padding-top: 1vh;
    padding-bottom: 1vh;
}

nav {
    width: 100%;
    padding: 0;
    /* padding-left: 3vw; */
    text-align: center;
    /* border-top: solid 1px #ccc;
    border-bottom: solid 1px #ccc; */
}

nav > ul {
    list-style: none;
    padding: 0;
    margin: 0;
    display: inline-block;
    /* background: #eee; */
    border-radius: 5px;
}

nav > ul > li {
    float: left;
    /* padding-left: 1.5vw;
    padding-right: 1.5vw;  */
    /* width: 150px; */
    height: 50px;
    line-height: 50px;
    position: relative;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 1vw;
    color: #3da290;
    cursor: pointer;
}

nav > ul > li:hover {
    background: #d5d5d5;
    border-radius: 5px;
}

ul.drop-menu {
    position: absolute;
    top: 100%;
    left: 0%;
    width: 14vw;
    padding: 0;
    list-style: none;
}

ul.drop-menu li {
    background: #666;
    /* color: rgba(255, 255, 255, 0.7); */
    color: white;
    /* padding: 0 1vw 0 1vw; */
    font-size: 0.85vw;
}

ul.drop-menu li {
    display: none;
}
  
ul.drop-menu li:hover {
    background: #3da290;
    color: white;
}

ul.drop-menu li:last-child {
    border-radius: 0px 0px 5px 5px;
}

li:hover > ul.drop-menu li {
    display: block;
    opacity: 0;
    animation: menu 0.5s ease-in forwards;
}

ul.drop-menu{
    perspective: 1000px;
}

li:hover > ul.drop-menu li:nth-child(1) { animation-duration: 0.3s }
li:hover > ul.drop-menu li:nth-child(2) { animation-duration: 0.5s }
li:hover > ul.drop-menu li:nth-child(3) { animation-duration: 0.7s }
li:hover > ul.drop-menu li:nth-child(4) { animation-duration: 0.9s } 

@keyframes menu{
    0%{
      opacity: 0;
      transform: rotateY(90deg) translateY(30px)
    }
    100%{
      opacity: 1;
      transform: rotateY(0deg) translateY(0px)
    }
  }
  